export default{
	props: {
		type: {
			required: true
		},
		itemData: {
			type: Object
		}
	},
	data() {
		return {
			price: 0.00,
			checkIndex: 0,
			detail: '',
			typeList: [],  		//类型集合
			PATH_DOMAIN: this.$SERVICE_DOMAIN,
		}
	},
	created() {
		this.queryTypeList();
	},
	methods: {
		queryTypeList() {  //获取相应分类
			this.$ajax('/billType/query').then(res => {
				if(res.status) {
					const typeList = [];
					res.data.forEach(item => {
						if(this.itemData.id) {
							if(item.type === this.itemData.type) {
								typeList.push(item);
							}
						} else {
							if(item.type === this.type) {
								typeList.push(item);
							}
						}
					});
					this.typeList = typeList;
					this.getCheckIndex();
				} else {
					console.log('分类获取失败');
				}
			});
		},
		checkItem(index) {  //改变选中状态
			this.checkIndex = index;
		},
		//解析 itemData，赋值到页面
		getCheckIndex() {
			if(!this.itemData || !this.itemData.hasOwnProperty('id')) {
				return;
			}
			this.price = this.itemData.price;
			this.detail = this.itemData.detail;
			let index = this.typeList.findIndex(item => {
				return item.img_src === this.itemData.img_src;
			});
			if(index !== -1) {
				this.checkIndex = index;
			} else {
				console.log('传递参数有误，未找到该分类');
			}
		},
		handlerSave() {
			if(this.price === 0) {
				uni.showToast({
					title: '请输入金额'
				});
				return;
			}
			const dt = {
				type: this.type,
				price: this.price,
				bill_type: this.typeList[this.checkIndex].id,
				detail: this.detail
			};
			if(this.itemData.id) {
				dt.id = this.itemData.id
			}
			this.$ajax(`${this.itemData.id ? '/bill/update' : '/bill/add'}`, dt).then(res => {
				if(res.status) {
					uni.showToast({
						icon: 'success',
						title: res.msg
					});
					this.price = 0.00;
					this.checkIndex = 0;
					this.detail = '';
					uni.navigateBack();
				}
			});
		},
		
		confirmDel() {
			uni.showModal({
			    title: '提示',
			    content: '您确定要删除吗?',
			    success: (res) => {
			        if (res.confirm) {
			            this.handlerDel();
			        }
			    }
			});
		},
		handlerDel() {
			this.$ajax('/bill/delete', {id : this.itemData.id}).then(res => {
				if(res.status) {
					uni.showToast({
						icon: 'success',
						title: res.msg
					});
					uni.navigateBack();
				}
			});
		}
	}
}
